var drawGraph = function drawGraph(id3Model, divId) {
    var g = new Array();
    g = addEdges(id3Model, g).reverse();
    window.g = g;
    var data = google.visualization.arrayToDataTable(g.concat(g));
    var chart = new google.visualization.OrgChart(document.getElementById(divId));
    google.visualization.events.addListener(chart, 'ready', function () {
        _.each($('.google-visualization-orgchart-node'), function (x) {
            var oldVal = $(x).html();
            if (oldVal) {
                var cleanVal = oldVal.replace(/_r[0-9]+/, '');
                $(x).html(cleanVal);
            }
        });
    });
    chart.draw(data, { allowHtml: true });
};

var addEdges = function addEdges(node, g) {
    if (node.type == 'feature') {
        _.each(node.vals, function (m) {
            g.push([m.alias, node.alias, '']);
            g = addEdges(m, g);
        });
        return g;
    }
    if (node.type == 'feature_value') {

        g.push([node.child.alias, node.alias, '']);
        if (node.child.type != 'result') {
            g = addEdges(node.child, g);
        }
        return g;
    }
    return g;
};

var renderSamples = function renderSamples(samples, $el, model, target, features) {
    _.each(samples, function (s) {
        var features_for_sample = _.map(features, function (x) {
            return s[x];
        });
        $el.append("<tr><td>" + features_for_sample.join('</td><td>') + "</td><td><b>" + predict(model, s) + "</b></td><td>actual: " + s[target] + "</td></tr>");
    });
};

var renderTrainingData = function renderTrainingData(_training, $el, target, features) {
    _training.each(function (s) {
        $el.append("<tr><td>" + _.map(features, function (x) {
            return s[x];
        }).join('</td><td>') + "</td><td>" + s[target] + "</td></tr>");
    });
};

var calcError = function calcError(samples, model, target) {
    var total = 0;
    var correct = 0;
    _.each(samples, function (s) {
        total++;
        var pred = predict(model, s);
        var actual = s[target];
        if (pred == actual) {
            correct++;
        }
    });
    return correct / total;
};

